@import "@/styles/colors.scss";

















































































.todo-item {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 1.25rem;

  &--checked .todo-item__description {
    text-decoration: line-through;
  }

  &__actions {
    display: flex;

    &__delete {
      fill: $pink;
      margin-right: 1.25rem;
      transition: all 0.3s ease-in;
      width: 1rem;

      &:hover {
        cursor: pointer;
        fill: darken($pink, 20%);
      }
    }
  }

  &__description {
    margin: 0;
  }
}
