@import "@/styles/colors.scss";










































.user {
  position: relative;

  &__name {
    color: $white;
    font-weight: bold;
  }

  &__menu {
    align-items: center;
    background-color: $blue;
    border: 2px solid $white;
    border-radius: 0.25rem;
    box-shadow: 0px 2px 5px rgba($dark-blue, 0.3);
    display: flex;
    justify-content: center;
    position: absolute;
    padding: 1.25rem;
    right: 0;
    top: 1.75rem;
    min-width: 12rem;
    z-index: 9;
  }
}
