@import "@/styles/colors.scss";








































































.header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 2.5rem 0;

  &__title {
    color: $white;
    margin: 0;
  }
}

.todos-add {
  display: flex;

  &__button {
    margin-left: 0.75rem;
  }

  &__controls {
    display: flex;
    margin-left: auto;
  }
}

.todos-container {
  background-color: $white;
  border: 2px solid $blue;
  border-radius: 1.25rem;
  margin-bottom: 1.75rem;
}

.todos-empty {
  padding: 1.25rem;
}
